import React from "react";

import moment from "moment";

const About = () => {
  function getAge() {
    var birthday = moment("1998-07-22");
    var today = moment();

    return today.diff(birthday, "years");
  }

  return (
    <div className="About">
      <section className="me">
        <img src="images/me.jpg" alt="alexander" />
      </section>
      <section className="info">
        <p>
          <img src="images/name.png" alt="name" title="name" />
          <span>Alexander Vashchuk</span>
        </p>
        <p>
          <img src="images/phone.png" alt="phone" title="phone" />
          <span>+380 99 75 45 105</span>
        </p>
        <p>
          <img src="images/email.png" alt="email" title="email" />
          <span>1alexvash@gmail.com</span>
        </p>
        <p>
          <img src="images/age.png" alt="age" title="age" />{" "}
          <span>{getAge()}</span>
        </p>
        <p>
          <img src="images/skype.png" alt="skype" title="skype" />
          <span>1alexvash</span>
        </p>
      </section>
    </div>
  );
};

export default About;
